//
// Visibility
// --------------------------------------------------

/* Responsive no javascript */
.no-js {
  @each $size in $breakpoint-classes {
    .nojs-for-#{$size} {
      @include hide-for($size);
    }

    .nojs-for-#{$size}-only {
      @include hide-for-only($size);
    }
  }
}



