//
// Forms
// --------------------------------------------------

/** Search form */
.search-form {
  @extend .input-group;
}

.search-form label {
  font-weight: normal;
}

.search-form .search-field {
  @extend .input-group-field;
}

.search-form .search-submit {
  @include button(false, $secondary-color, auto, $button-color, $button-fill);
}



